    // 	let url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=7023ec1c5cc24419ae474e6bbf62f42a&page=${this.state.page + 1}&pageSize=${props.pageSize}`;
